import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import Table from "react-bootstrap/Table";

import { getList } from "../AppStore/actions";
import TableItem from "./tableItem";
import Pagination from "../Shared/Pagination";
import styles from "./table.module.css";

const tableHeader = {
  id: "User ID",
  name: "Name",
  email: "Email",
  username: "Username",
  website: "Website",
  phone: "Phone",
  city: "City",
  company: "Company",
};

const TableView = (props) => {
  const { getList, userList } = props;
  const [searchQuery, setSearchQuery] = useState("");
  const [items, setItems] = useState([]);
  const [page, setPage] = useState(1);
  const [sortKey, setSortKey] = useState("");
  const itemsPerPage = 5;

  useEffect(() => {
    getList();
  }, [getList]);

  useEffect(() => {
    setInitialTableState();
  }, [userList]);

  const setInitialTableState = () => {
    setSearchQuery("");
    setPage(1);
    setSortKey("");
    const updatedUserList = [...userList];
    setItems(updatedUserList.slice(0, itemsPerPage));
  };

  const handleChangePage = (pageNumber) => {
    const updatedItems = [...userList];

    setItems(
      updatedItems.slice(
        (pageNumber - 1) * itemsPerPage,
        pageNumber * itemsPerPage
      )
    );

    setPage(pageNumber);
    setSearchQuery("");
  };

  const handleSearch = (e) => {
    setSearchQuery(e.target.value);

    const updatedItems = [...userList];

    const filteredItems = [];

    const currestSetOfItems = updatedItems.slice(
      (page - 1) * itemsPerPage,
      page * itemsPerPage
    );

    currestSetOfItems.forEach((dataItem) => {
      if (
        dataItem.name &&
        dataItem.name.toLowerCase().includes(e.target.value.toLowerCase())
      ) {
        filteredItems.push(dataItem);
      }
    });

    setItems(filteredItems);
  };

  const handleSort = (key) => {
    const updatedItems = [...items];

    if (key !== sortKey) {
      setItems(ascSort(updatedItems)(key));
    } else {
      setItems(descSort(updatedItems)(key));
    }

    setSortKey(key === sortKey ? "" : key);
  };

  const ascSort = (list) => (key) =>
    list.sort(function (a, b) {
      return a[key] > b[key] ? 1 : -1;
    });

  const descSort = (list) => (key) =>
    list.sort(function (a, b) {
      return a[key] > b[key] ? -1 : 1;
    });

  return (
    <div className={styles.container}>
      <div className="table-view">
        <div className={styles.headerContainer}>
          <input
            placeholder="Search by Name"
            value={searchQuery}
            onChange={handleSearch}
          />
          <button onClick={setInitialTableState}>Reset</button>
        </div>
        <Table>
          <thead>
            <tr>
              {Object.keys(tableHeader).map((key) => {
                return (
                  <th
                    onClick={() => {
                      handleSort(key);
                    }}
                    onMouseOver={(e) => {
                      e.currentTarget.style.backgroundColor = "lightgray";
                    }}
                    onMouseOut={(e) => {
                      e.currentTarget.style.backgroundColor = "white";
                    }}
                  >
                    <span>{tableHeader[key]}</span>
                    <span
                      className={`${
                        sortKey === key ? styles.sortIconActive : ""
                      } ${styles.sortIcon}`}
                    >
                      {">"}
                    </span>
                  </th>
                );
              })}
            </tr>
          </thead>
          <tbody>
            {items.map((item, index) => (
              <TableItem key={item.id} item={item} index={index} />
            ))}
          </tbody>
        </Table>
        <Pagination
          page={page}
          totalItems={userList.length}
          itemsPerPage={itemsPerPage}
          handleChange={handleChangePage}
        />
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    userList: state.tableListReducer.userList || [],
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getList: () => dispatch(getList()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TableView);
